<template>
  <div class="hello">
    <el-row>
      <el-col :span="4"><div class="grid-content bg-purple"></div></el-col>

      <el-col :span="16">
        <div class="grid-content bg-purple-light">
          <p>{{ msg }}</p>
          <el-input v-model="input" placeholder="请输入内容"></el-input>
          <p style="color: red;font-size: 12px">{{md5Str}}</p>
        </div>
      </el-col>

      <el-col :span="4" style=" margin-top: 82px;text-align: left">
        <div class="grid-content bg-purple">
          <el-button style="margin-left: 5px" @click="md5btn()" icon="el-icon-search" circle></el-button>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
    name: 'HelloWorld',
    data(){
      return{
          input:'',
          md5Str:''
      }
    },
    methods:{
        md5btn(){
            let _this = this;
            let str = this.input;
            this.axios.post('index/Index/getMd5Encode',{'str':str})
                .then(function(response){
                    _this.md5Str = '解密后字符串为：'+response.data.str;
                    if(response == null){
                        _this.md5Str = '解密失败';
                    }
                }).catch(function (err){
                    _this.md5Str = '解密失败';
                    console.log('解密失败'+err);
            });
        }
    },
    props: {
      msg: String
    }
}
</script>

